<template>
    <div class="paragraph fl">
        <div v-if="paragraph">
            <div class="paragraph-title bold">
                {{main.title}}
            </div>
            <div class="paragraph-content" id="paragraph" v-html="main.content">

            </div>
        </div>
        <div v-else>
            <div class="paragraph-title bold">
                问题列表
            </div>
            <div class="paragraph-content">
                <ul>
                    <li class="title" v-for="(i,k) in artlist" v-if="selectCid?i.cid===selectCid:true">
                        <a @click="toArticle(i.id,i.title)">{{i.title}}</a>
                    </li>
                    <li v-if="artlist.length === 0">什么都没有</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex"
    export default {
        name: 'paragraph',
        data() {
            return {
                main:{}
            }
        },
        computed:{
            ...mapState('article',['artlist','selectCid']),
            paragraph(){
                if (this.$route.params.aid){
                    return true
                }else {
                    return false
                }
            }
        },
        mounted(){
            if (this.$route.params.aid){
                this.getArticle(this.$route.params.aid)
            }else{
                this.$store.dispatch('article/chooesedName',null);
            };
        },
        methods:{
            getArticle(id){
                this.yum('api/article/getArticle',{articleId:id}).then(res=>{
                    if (res.data.status === 200 ){
                        this.main = res.data.data[0]
                    }
                })
            },
            toArticle(id,name){
                this.$store.dispatch('article/chooesedName',name);
                this.getArticle(id);
                this.$router.push(`/${id}`);
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .paragraph
        width 770px
        border 1px solid #dcdcdc
        padding 0 20px
        &-title
            padding 30px 0
            font-size 18px
            color black
            border-bottom 1px solid #dcdcdc
        &-content
            padding 30px 0
            position relative
            .title
                margin-bottom 20px
</style>
